






































































































.avatar {
  text-align: center;
  margin-bottom: 50px;
}
.swipe-navigation {
  color: #654d8d;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 2rem;
  &--left {
    left: 10%;
  }
  &--right {
    right: 10%;
  }
}
.form {
  margin-bottom: 40px;
}
